import React from 'react';
import CircularProgress from '@material-ui/core/CircularProgress';
import Container from '@material-ui/core/Container';

import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  mainContainer: {
    width:'100%',
    maxWidth:'100%',
    minWidth:'100%',
    display:'flex',
    alignContent:'center',
    alignItems:'center',
    alignSelf:'center',
    justifyContent:'center',
    justifySelf:'center',
    padding:'40px'

  },
});

const handleUpload = function (arrayWithAllFilesConfigSettings) {
    /* console.log("uploading") */
    /* const files = event.target.files */
    let formData = new FormData()
    /* let objArr = {}; */
    for(let i=0; i<arrayWithAllFilesConfigSettings.length;i++){
        //console.log(arrayWithAllFilesConfigSettings[i].rawFile)
        formData.append("images",arrayWithAllFilesConfigSettings[i].rawFile)
        
    }
    /* formData.append("images",arrayWithAllFilesConfigSettings[0].rawFile) */
    /* console.log(formData.entries()) */
    
    fetch('http://localhost:5000/workingupload', {
      method: 'POST',
      body: formData,
      /* headers: {
        "Content-Type": "multipart/form-data"
      } */
    })
    .then(response => response.json())
    .then(data => {
      console.log(data)
    })
    .catch(error => {
      console.error(error)
    })
  }

export default function PaymentGateway(props){
    const classes = useStyles();
    /* console.log(props.arrayWithAllFilesConfigSettings[0].rawFile)
    fetch('/',) */
    handleUpload(props.arrayWithAllFilesConfigSettings)
    return(
      <Container className={classes.mainContainer}>
        <CircularProgress></CircularProgress>
      </Container>
    )
}